export default "import React from 'react';\r\nimport {\r\n  Area,\r\n  AreaChart,\r\n  CartesianGrid,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport data from './data';\r\n\r\nconst StackedAreaChart = () => (\r\n  <ResponsiveContainer width='100%' height={200}>\r\n    <AreaChart data={data} margin={{top: 10, right: 0, left: -25, bottom: 0}}>\r\n      <XAxis dataKey='name' />\r\n      <YAxis />\r\n      <CartesianGrid strokeDasharray='3 3' />\r\n      <Tooltip />\r\n      <Area\r\n        type='monotone'\r\n        dataKey='uv'\r\n        stackId='1'\r\n        stroke='#4299E1'\r\n        fill='#4299E1'\r\n      />\r\n      <Area\r\n        type='monotone'\r\n        dataKey='pv'\r\n        stackId='1'\r\n        stroke='#59AA2B'\r\n        fill='#59AA2B'\r\n      />\r\n      <Area\r\n        type='monotone'\r\n        dataKey='amt'\r\n        stackId='1'\r\n        stroke='#F04F47'\r\n        fill='#F04F47'\r\n      />\r\n    </AreaChart>\r\n  </ResponsiveContainer>\r\n);\r\n\r\nexport default StackedAreaChart;\r\n";